/* RegistrationForm.css */

.logc {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    /* background-color: rgba(0, 0, 0, 0.5); Transparent black background */
  }
  
  .logc22 {
    background-color: transparent;
    background-color:rgb(0, 0, 0);
    color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  }
  
  .inside2 {
    margin-top: 30px;
    margin-left: 40px;
    margin-right: 40px;
  }
  
  label {
    display: block;
    font-weight: bold;
    color: #333;
  }
  
  input {
    width: 100%;
    padding: 10px;
    margin-top: 5px;
    margin-bottom: 15px;
    border: 3px solid rgb(112, 212, 220);
    border-radius: 5px;
    box-sizing: border-box; /* Include padding and border in the width */
  }
  
  input::placeholder {
    color: #999;
  }
  
  input:focus {
    border-color: #007bff; /* Example color */
    outline: none;
  }
  
  .butn {
    text-align: center;
  }
  
  .btn1 {
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .btn1:hover {
    background-color: #45a049;
  }
  
  .acc {
    margin-top: 20px;
    text-align: center;
  }
  
  .acc a {
    color: #007bff;
    text-decoration: none;
  }
  
  .acc a:hover {
    text-decoration: underline;
  }
  