/* Your CSS file */

.inside {
    margin-top: 20px;
  }
  
  label {
    display: block;
    font-weight: bold;
    color: #000000;
  }
  
  input {
    width: 100%;
    padding: 10px;
    margin-top: 5px;
    margin-bottom: 15px;
    border: 3px solid rgb(112, 212, 220);
    border-radius: 5px;
    box-sizing: border-box; /* Include padding and border in the width */
  }
  
  input::placeholder {
    /* Styles for placeholder text */
    color: #999;
  }
  
  input:focus {
    /* Styles when the input is focused */
    border-color: #007bff; /* Example color */
    outline: none;
  }
  
  /* Add more styles as needed */
  
  
  
